.header {
  position: relative;
}

.header-desktop {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 114px;
  padding-top: 10px;
  margin: auto;
}

.header-right {
  display: flex;
  align-items: center;
}

.header-menu {
  display: flex;
  margin-right: 36px;
}
.header-menu ul {
  display: flex;
  flex-direction: row;
  list-style: none;
  gap: 43px;
  padding-left: 0;
}
.header-menu::after {
  content: "";
  width: 1px;
  height: 22px;
  background-color: rgba(0, 0, 0, 0.2);
  transform: translate(18px, 13px);
}

.header-phones {
  font-weight: 600;
  color: var(--font-black);
}

.header-search {
  cursor: pointer;
}

.header-mobile {
  position: absolute;
  z-index: 100;
  flex-direction: column;
  width: 100%;
  background-color: #fff;
  right: 0;
  display: none;
}
.header-mobile .header-menu ul {
  flex-direction: column;
}

.menu-button-container {
  display: none;
  height: 100%;
  width: 30px;
  cursor: pointer;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#menu-toggle {
  display: none;
}

.menu-button,
.menu-button::before,
.menu-button::after {
  display: block;
  background-color: #fff;
  position: absolute;
  height: 4px;
  width: 30px;
  transition: transform 400ms cubic-bezier(0.23, 1, 0.32, 1);
  border-radius: 2px;
}

.menu-button::before {
  content: "";
  margin-top: -8px;
}

.menu-button::after {
  content: "";
  margin-top: 8px;
}

#menu-toggle:checked + .menu-button-container .menu-button::before {
  margin-top: 0px;
  transform: rotate(405deg);
}

#menu-toggle:checked + .menu-button-container .menu-button {
  background: rgba(255, 255, 255, 0);
}

#menu-toggle:checked + .menu-button-container .menu-button::after {
  margin-top: 0px;
  transform: rotate(-405deg);
}

@media screen and (max-width: 1260px) {
  .header-menu ul {
    gap: 15px;
  }
}
@media screen and (max-width: 1150px) {
  .header {
    position: fixed;
    background-color: var(--footer);
    width: 100%;
  }
  .menu-button-container {
    display: flex;
  }
  .header-menu ul {
    position: absolute;
    top: 0;
    margin-top: 50px;
    left: 0;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
