       transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
     }

     > li {
       display: flex;
       justify-content: center;
       margin: 0;
       padding: 0.5em 0;
       width: 100%;
       color: white;
       // background-color: #222;
     }

     > li:not(:last-child) {
       border-bottom: 1px solid #444;
     } */
  }
  .header-menu ul li {
    height: 0;
    margin: 0;
    padding: 0;
    border: 0;
    transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
  }
  .header-menu ul li a {
    display: block;
    height: 0;
    margin: 0;
    padding: 0;
    border: 0;
    transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
  }
  .open-menu {
    border: 1px solid #333;
    height: 2.5em;
    padding: 0.5em;
    transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
  }
}